  --dark-accordion-color: #474747;
  --logo-text-color: #173a56;
  --green-color: #02897a;
  --blue-color: #4d8dff;
  --purple-color: #740a76;
  --red-color: #f03e3d;
  --yellow-color: #f90;
  --white-color: #fff;
  --border-color: #dedede;
  --color-for-mobile-form: #bcd0e5;
  --form-color: #f5f5f5;
  --form-border-color: #e8e8e8;
  --background-color-for-sections: #f5f5f5;
  --background-color-for-business: #ffefef;
